<template>
  <div class="table1">
    <div class="add cb">
      <div class="add-left fl">周报推荐</div>
    </div>
    <el-table ref="multipleTable" :data="weeklyList" tooltip-effect="dark" type="index" style="width: 100%">
      <el-table-column prop="staff" label="推荐人" show-overflow-tooltip>
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row|showStaffName}}</div>
        </template>
      </el-table-column>  
      <el-table-column prop="cwl" label="周报名称" show-overflow-tooltip>
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row|showsubjectName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="cwl" label="关键字标签" show-overflow-tooltip>
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row|showkeyWord}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="cwl" label="归属组"  width="110">
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row|showsubjectGroupTypeValue}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="time" label="推荐时间" width="190">
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row.time}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template scope="scope">
            <el-button type="text" @click="deletelist(weeklyList.weeklyId,scope.$index)"><i class="el-icon-delete delete"></i></el-button>           
        </template>
			</el-table-column>
		</el-table>
    <div class="hello">      
      <el-pagination  @size-change="pageSizeChange" @current-change="currentPageChange" :current-page="currentPage" :page-sizes="[ 5,10 ]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination> 
    </div>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  name: "table1",
  data() {
    return {
      addDetail: {},
      editDetail: {},
      index: 0,
      weeklyList: [],
      subjectName: "",
      staffname: "",
      staffnames: "",
      staffID: "",
      editid: "",
      //搜索表单
      searchForm: {
        id: "",
        name: "",
        state: ""
      },
      //多选值
      multipleSelection: [],
      //当前页
      currentPage: 1,
      //分页大小
      pageSize: 10,
      //总记录数
      total: ""
    };
  },
  mounted: function() {
    this.getList();
    this.getAauthor();
  },
  filters: {
    showStaffName(row) {
      if (row.staff) {
        return row.staff.staffname;
      } else {
        return "";
      }
    },
    showsubjectName(row) {
      if (row.cwl) {
        return row.cwl.subjectName;
      } else {
        return "";
      }
    },
    showsubjectGroupTypeValue(row) {
      if (row.cwl) {
        return row.cwl.subjectGroupTypeValue;
      } else {
        return "";
      }
    },
    showkeyWord(row) {
      if (row.cwl) {
        return row.cwl.keyWord;
      } else {
        return "";
      }
    }
  },
  methods: {
    getList() {
      let params = {
        pages: this.currentPage,
        pageSize: this.pageSize
      };
      console.log(params);
      service
        .goodWeeklyList(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.weeklyList = res.data.list;
          this.total = res.data.total;
          console.log(this.weeklyList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[3];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
        })
        .catch(err => {
          console.log(err);
        });
    },
    //删除
    deletelist(weeklyId, index) {
      if (this.weeklyList[index].topStaffId == this.staffID) {
        this.$confirm("确认取消推荐？")
          .then(_ => {
            let params = {
              topId: this.weeklyList[index].topId,
              staffId: this.staffID
            };
            console.log(params);
            service
              .delGoodWeeklyList(params)
              .then(response => {
                console.log(response);
                // 加载列表
                this.getList();
              })
              .catch(error => {
                console.log(error);
                alert("删除失败！");
              });
          })
          .catch(_ => {});
      } else {
        console.log("不可以删除他人的周报");
      }
    },
    //分页大小修改事件
    pageSizeChange: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSize = val;
      this.getList();
    },
    //当前页修改事件
    currentPageChange: function(val) {
      this.currentPage = val;
      console.log("当前页: " + val);
      this.getList();
    },
    knowledgeOper(row) {
      console.log(row);
      let routeData = this.$router.resolve({
        path: "/weeklyDetail",
        query: {
          id: row.weeklyId
        }
      });
      window.open(routeData.href, "_blank");
    }
  }
};
</script>

<style scoped>
table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #eee;
}
table thead th {
  background: #e5eff8;
  padding: 22px 18px 22px 18px;
  text-align: center;
}
table tbody td {
  padding: 10px 0px 10px 18px;
  text-align: left;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}
table span {
  margin: 0 10px;
  cursor: pointer;
}
.delete {
  color: red;
}
.edit {
  color: #008cd5;
}
.add {
  border: 1px solid #eee;
  margin-bottom: 10px;
  height: 65px;
  padding: 15px 0px 15px 15px;
}
tbody tr:hover {
  background-color: #e5f0f86c;
}
.add button {
  background: #008cd5;
  border: 0;
  cursor: pointer;
  padding: 6px 15px;
  border-radius: 3px;
  color: #fff;
  margin-left: 10px;
}
.hello {
  margin-top: -10px;
  margin-bottom: 10px;
}
.header-search-input {
  display: inline-block;
  right: 1%;
  margin-left: 12px;
  width: 300px;
}
.header-search-input input {
  position: relative;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #d8dce5;
  display: inline-block;
  height: 35px;
  line-height: 1;
  outline: 0;
  padding: 0 25px 0 15px;
  width: 280px;
  margin-right: -4px;
}
.query-search-btn {
  color: #c2d6e7;
  position: relative;
  display: inline-block;
  margin-left: -30px;
  cursor: pointer;
}
.add-left {
  font-size: 16px;
}
.hello {
  position: fixed;
  bottom: 0px;
  right: 20px;
}
</style>


